.header {
  position: fixed;
  z-index: 10;
  width: 100vw;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;
  background: $background;
  box-shadow: 1px 1px 1px $cta-color;
  &--nav-burger {
    @media (max-width: $medium-min) {
      display: block;
      cursor: pointer;
      div {
        width: 35px;
        height: 4px;
        content: "";
        background: #fff;
        top: 30px;
        z-index: 20;
        transition: 0.5s;
        right: 5%;
        border-radius: 15px;
        position: fixed;
      }
      .burger-1 {
        transition: 0.5s;
      }
      .burger-2 {
        top: 37px;
        transition: 0.5s;
      }
      .burger-3 {
        top: 44px;
        transition: 0.5s;
      }
    }
    @media (max-width: 330px) {
        div {
            top: 15px;
          }
          .burger-1 {
            transition: 0.5s;
          }
          .burger-2 {
            top: 22px;
          }
          .burger-3 {
            top: 29px;
          }
    }
  }
  @media (max-width: $mobile-max) {
    top: 0;
  }
  
}
.header--logo {
  padding-top: 10px;
  width: 195px;
  height: 67px;
  img {
    width: 100%;
  }
}
.header--buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  height: 50px;
  transition: 0.8s;
  color: #fff;
  @media (max-width: $medium-min) {
    position: absolute;
    left: 0;
    display: block;
    right: 0;
    margin-top: 110px;
    text-align: center;
    width: 100%;
    height: auto;
    padding: 10px 0;
    z-index: 99999;
    background: #fff;
    border-top: 1px solid $violet;
    border-bottom: 1px solid $violet;
  }
  &-1, &-2 {
    width: 180px;
    padding: 5px 0;
    text-align: center;
    border-radius: 40px;
    cursor: pointer;
    background-color: $cta-color;
    @media (max-width: 768px) {
      width: 70%;
      margin: 10px auto;
      background: none;
      color: $violet;
      height: 50px;
      padding: 10px 0;
      text-align: left;
      border-radius: 0;
    }
    @media (min-width: 769px) {
        margin-right: 60px;
    }
    @media (max-width: $medium-min) {
        border-bottom: 1px solid $violet;
    }
    a {
        @media (min-width: 769px) {
            color: #FFF;
        }
        &:hover {
            color: $cta-color;
        }
    }
    &:hover {
        background-color: #FFF;
    }
}
}
